import React, { useState } from 'react';
import Success from "./Success";

export default (props) => {
  const [errors, setErrors] = useState({
    email: false,
    telegram: false,
    message: false,
  });
  const [success, setSuccess] = useState(false);

  const onChange = (fieldName) => (ev) => {
    setErrors({ ...errors, [fieldName]: !ev.target.value });
  };

  const onSendBtn = () => {
    setSuccess(true);
  }

  return (
    <React.Fragment>
      <div id="modal">
        <div className="modal_wrap">
          <div className="modal contact_modal">
            <div className="title_block">
              <h6>Contact us</h6>
              <div className="close" onClick={() => props.close(false)}></div>
            </div>
            <div className="scroll_modal">
              <form className="contact_form">
                <label className="input_label">
                  <input type="text" className="input" placeholder=" " />
                  <span className="placeholder">
                    <span className="prev_plh"></span>
                    <span className="text_plh">Name</span>
                    <span className="last_plh"></span>
                  </span>
                </label>
                <label className="input_label">
                  <input type="email" className="input" placeholder=" " onChange={onChange('email')} />
                  <span className="placeholder">
                    <span className="prev_plh"></span>
                    <span className="text_plh">E-mail</span>
                    <span className="last_plh"></span>
                  </span>
                  <span className="error">{errors.email && 'Lorem ipsum'}</span>
                </label>
                <label className="input_label">
                  <input type="text" className="input" placeholder=" " onChange={onChange('telegram')} />
                  <span className="placeholder">
                    <span className="prev_plh"></span>
                    <span className="text_plh">Telegram</span>
                    <span className="last_plh"></span>
                  </span>
                  <span className="error">{errors.telegram && 'Lorem ipsum'}</span>
                </label>
                <label className="input_label">
                  <textarea className="input" placeholder=" " onChange={onChange('message')}></textarea>
                  <div className="placeholder">
                    <span className="prev_plh"></span>
                    <span className="text_plh">Message</span>
                    <span className="last_plh"></span>
                    <div className="before"></div>
                  </div>
                  <span className="error">{errors.message && 'Lorem ipsum'}</span>
                </label>
                <button className="all_btn" onClick={onSendBtn}>Send</button>
              </form>
            </div>
          </div>
        </div>
      </div>
        { success ? <Success close={ setSuccess }/> : null }
    </React.Fragment>
  );
};



